import React from "react";

const ResultsComponent = ({ filteredResorts }) => {
  return (
    <div>
      <h2> Results</h2>
      {filteredResorts.length > 0 ? (
        filteredResorts.map((resort, index) => (
          <div key={index}>
            <h3>{resort.name}</h3>
            <p> Location: {resort.location}</p>
            <p> Base Elevation: {resort.baseElevation}</p>
            <p> Highest Elevation: {resort.highestElevation}</p>
            <p> Average Snowfall: {resort.averageSnowfall}</p>
            <p> Skiable Terrain: {resort.skiableTerrain}</p>
            <p> Lifts: {resort.lifts}</p>
            <p> Trails: {resort.trails}</p>
            <p> Terrain Parks: {resort.terrainParks}</p>
            <p> Most Difficult Lift: {resort.mostDifficultLift}</p>
            <p> Beginner Terrain: {resort.terrainDifficulty.beginner}</p>
            <p> Intermediate Terrain: {resort.terrainDifficulty.intermediate}</p>
            <p> Advanced Terrain: {resort.terrainDifficulty.advanced}</p>
            <p> Most Difficult Terrain: {resort.terrainDifficulty.mostDifficult}</p>
            <p> Expert Only Terrain: {resort.terrainDifficulty.expertOnly}</p>
          </div>
        ))
      ) : (
        <p>No resorts found. Try adjusting your filters.</p>
      )}
    </div>
  );
};

export default ResultsComponent;