import React, { useState, useEffect } from "react";
import { io } from "socket.io-client";

const socket = io("http://localhost:3001");

const DistanceFinder = () => {
  const [distances, setDistances] = useState({});
  const [lat, setLat] = useState("");
  const [lon, setLon] = useState("");
  const [userLocation, setUserLocation] = useState("");

  useEffect(() => {
    socket.on("connect", () => console.log("Connected to WebSocket Server"));
    socket.on("distance-update", (updatedDistances) => setDistances(updatedDistances));

    return () => socket.disconnect();
  }, []);

  const getUserLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        const userLat = position.coords.latitude.toFixed(4);
        const userLon = position.coords.longitude.toFixed(4);
        setLat(userLat);
        setLon(userLon);
        setUserLocation(` Your Location: ${userLat}, ${userLon}`);
      }, () => alert(" Location access denied. Enable GPS."));
    } else {
      alert(" Geolocation is not supported by this browser.");
    }
  };

  const sendUserLocation = () => {
    if (!lat || !lon) {
      alert(" Please enter valid coordinates or use ' Use My Location'");
      return;
    }
    socket.emit("user-location", { lat: parseFloat(lat), lon: parseFloat(lon) });
  };

  return (
    <div>
      <h1> Find Your Ski Resort Distance</h1>
      <button onClick={getUserLocation}> Use My Location</button>
      <p style={{ color: "#007bff" }}>{userLocation}</p>

      <label>Latitude:</label>
      <input type="text" value={lat} onChange={(e) => setLat(e.target.value)} placeholder="Enter your latitude" /><br/>

      <label>Longitude:</label>
      <input type="text" value={lon} onChange={(e) => setLon(e.target.value)} placeholder="Enter your longitude" /><br/>

      <button onClick={sendUserLocation}> Get Distance</button>

      <h2>Distances to Ski Resorts:</h2>
      {Object.entries(distances).map(([resort, distance]) => (
        <p key={resort}><strong>{resort}:</strong> {distance} miles</p>
      ))}
    </div>
  );
};

export default DistanceFinder;