import React, { useState, useEffect } from "react";
import FilterComponent from "../components/FilterComponent";
import ResultsComponent from "../components/ResultsComponent";

const SkiResortFilter = () => {
  const [resorts, setResorts] = useState([]);
  const [filteredResorts, setFilteredResorts] = useState([]);

  useEffect(() => {
    fetch("http://localhost:5000/api/resorts")
      .then((res) => {
        if (!res.ok) {
          throw new Error(`HTTP error! Status: ${res.status}`);
        }
        return res.json();
      })
      .then((data) => setResorts(data))
      .catch((err) => console.error("Error fetching resorts:", err));
  }, []);

  const handleFilterChange = (filters) => {
    const filtered = resorts.filter((resort) => {
      return Object.keys(filters).every((key) => {
        if (!filters[key]) return true;
        return resort[key]?.toString().toLowerCase() === filters[key].toLowerCase();
      });
    });

    setFilteredResorts(filtered.length > 0 ? filtered : resorts);
  };

  return (
    <div style={{ textAlign: "center", fontFamily: "Arial, sans-serif", marginTop: "20px" }}>
      <h1> Ski Resort Finder</h1>
      <FilterComponent onFilterChange={handleFilterChange} />
      <ResultsComponent filteredResorts={filteredResorts} />
    </div>
  );
};

export default SkiResortFilter;